@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100..900&display=swap");
html,
body {
  font-family: 'Libre Franklin', sans-serif;
  font-optical-sizing: auto;
  font-size: 14px; }

footer.site-footer .social-icons .circle-icon-link {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid currentColor;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 -7px; }
  footer.site-footer .social-icons .circle-icon-link svg {
    width: 20px;
    height: 20px;
    fill: white; }
  footer.site-footer .social-icons .circle-icon-link:hover {
    background-color: white; }
    footer.site-footer .social-icons .circle-icon-link:hover svg {
      fill: #0479bb; }

@media (min-width: 600px) {
  footer.site-footer .site-footer__grid {
    display: flex;
    gap: unset;
    justify-content: space-between; }
    footer.site-footer .site-footer__grid > div {
      width: calc(33% - 5rem);
      box-sizing: border-box; } }

form select,
form input {
  /* Overrides  and Exceptions */ }
  form select[type='text' i],
  form input[type='text' i] {
    border-radius: 5px;
    display: flex;
    height: 3.75rem;
    width: 100%;
    border: 1px solid #c3c3c3;
    background: #f9f9f9;
    font-weight: 600;
    padding: 0 1em; }
  form select,
  form input {
    padding: 10px;
    min-height: 30px;
    margin: 0;
    box-sizing: border-box;
    margin-top: 10px; }
    @media (min-width: 600px) {
      form select,
      form input {
        min-width: 600px; } }
  form select[name*='zip'],
  form input[name*='zip'] {
    min-width: auto; }

form textarea {
  width: 100%;
  padding: 10px;
  min-height: 100px;
  font-size: 22px;
  margin: 0;
  box-sizing: border-box;
  margin-top: 10px; }
  @media (min-width: 600px) {
    form textarea {
      min-width: 600px; } }

@media (min-width: 1200px) {
  .container {
    width: 1170px; } }

@media (min-width: 992px) {
  .container {
    width: 970px; } }

@media (min-width: 768px) {
  body {
    /* main background image */
    background: url("https://d2r0txsugik6oi.cloudfront.net/neon/resource/lcfamerica/templateImage/footerBackgroundImage_61.png?forwardedFromSecureDomain=1&r=25008611172196628") no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    margin: 0%; }
  .container {
    width: 750px; }
  .col-sm-12 {
    width: 100%; }
  .col-sm-1,
  .col-sm-10,
  .col-sm-11,
  .col-sm-12,
  .col-sm-2,
  .col-sm-3,
  .col-sm-4,
  .col-sm-5,
  .col-sm-6,
  .col-sm-7,
  .col-sm-8,
  .col-sm-9 {
    float: left; }
  #content {
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 800px; } }

