/* customizing general donation form */
.neoncrm-radio-group.neoncrm-donation-levels label {
  font-size: 24px;
  line-height: 48px;
  display: inline-block;
  padding: 5px 15px;
  color: #fff;
  background-color: #0479bb;
  margin: 10px;
  min-width: 48px;
  min-height: 48px;
  border: solid 1px #ddd;
  border-radius: 4px;
  text-align: center; }

.neoncrm-radio-group.neoncrm-donation-levels label:hover {
  background-color: #a6c75f;
  color: #fff; }

.neoncrm-radio-group.neoncrm-donation-levels input[type='radio'] {
  display: none; }

.neoncrm-radio-group.neoncrm-donation-levels input:checked + label {
  background-color: #895ad2;
  color: #fff; }

.neoncrm-donation-levels .neoncrm-radio-button {
  display: inline-block; }

@media (max-width: 600px) {
  .neoncrm-donation-levels .neoncrm-radio-button {
    display: block; }
  .neoncrm-radio-group.neoncrm-donation-levels label {
    display: block; } }

